//'use strict';

window.addEventListener('DOMContentLoaded', () => {
    // Tabs
    const tabParent = document.querySelector('.tabcontainer'),
           tabsContent = document.querySelectorAll('.tabcontent'),
           tabs = document.querySelectorAll('.tabheader__item');

    function enableTab(i = 0) {
        tabs[i].classList.add('tabheader__item_active');
        tabsContent[i].classList.remove('disable');
        tabsContent[i].classList.add('show');
    }

    function disableTab() {        
        tabsContent.forEach((item, i) => {
            item.classList.add('disable');
            item.classList.remove('show');
        });
           
        tabs.forEach((item, i) => {
            item.classList.remove('tabheader__item_active');
        });
    }
       
    tabParent.addEventListener('click', (event) => {
        const target = event.target;

        if(target && target.classList.contains('tabheader__item')) {
            tabs.forEach((item, i) => {
                if(item === target) {
                    disableTab();
                    enableTab(i);
                }
            });
        }
    });

    disableTab();
    enableTab();

    // Timer 
    const deadline = '2021-03-10';

    function getTimeRemaining(endtime) {
        const t = Date.parse(endtime) - Date.parse(new Date()),
            days = Math.floor( (t/(1000*60*60*24)) ),
            seconds = Math.floor( (t/1000) % 60 ),
            minutes = Math.floor( (t/1000/60) % 60 ),
            hours = Math.floor( (t/(1000*60*60) % 24) );

        return {
            'total': t,
            'days': days,
            'hours': hours,
            'minutes': minutes,
            'seconds': seconds
        };
    }

    function getZero(num){
        if (num >= 0 && num < 10) { 
            return '0' + num;
        } else {
            return num;
        }
    }

    function setClock(selector, endtime) {
        const final = getTimeRemaining(endtime);
 

        if (final.total > 0) {  

            const timer = document.querySelector(selector),
                days = timer.querySelector("#days"),
                hours = timer.querySelector('#hours'),
                minutes = timer.querySelector('#minutes'),
                seconds = timer.querySelector('#seconds'),
                timeInterval = setInterval(updateClock, 1000);

            updateClock();
        
            function updateClock() {
                const t = getTimeRemaining(endtime);

                days.innerHTML = getZero(t.days);
                hours.innerHTML = getZero(t.hours);
                minutes.innerHTML = getZero(t.minutes);
                seconds.innerHTML = getZero(t.seconds);

                if (t.total <= 0) {
                    clearInterval(timeInterval);
                    days.innerHTML = "00";
                    hours.innerHTML = "00";
                    minutes.innerHTML = "00";
                    seconds.innerHTML = "00";
                }
            }
        }
    }

    setClock('.timer', deadline);

    // Modal 
    const modalTrigger = document.querySelectorAll('[data-modal]'),
    modalWindow = document.querySelector('.modal');

    function openModal() {
        modalWindow.classList.add('show');
        document.body.style.overflow = 'hidded';

        clearInterval(modalTimer);
    }    
    
    function closeModal() {
        modalWindow.classList.remove('show');
        document.body.style.overflow = '';
    }

    modalTrigger.forEach(item => {
        item.addEventListener('click', openModal);
    });

    modalWindow.addEventListener('click', (e) => { 
        if (e.target === modalWindow || e.target.getAttribute('data-close') == '') { 
            closeModal();
        }
    });

    document.addEventListener('keydown', (e) => {
        if (e.code === 'Escape' && modalWindow.classList.contains('show')) {
            closeModal();
        }
    });

    const modalTimer = setInterval(openModal, 50000);

    function showModalByScroll() {
        if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight) {
            openModal();

            window.removeEventListener('scroll', showModalByScroll);
        }
    }

    window.addEventListener('scroll', showModalByScroll);

    // Dynamic menu card

    class MenuCard {
        constructor(imgSrc, alt, title, desk, prise, parentSelector, ...classes) {
            this.imgSrc = imgSrc;
            this.alt = alt;
            this.title = title;
            this.desk = desk;
            this.prise = prise;
            this.classes = classes;
            this.parent = document.querySelector(parentSelector);
            this.transfer = 27;
            this.changeToUAH();
        } 

        changeToUAH() {
            this.prise = this.prise * this.transfer;
        }

        render() { 
            const element = document.createElement('div');
            
            if (this.classes.length === 0) {
                this.element = 'menu__item';
                element.classList.add(this.element);
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }

            element.innerHTML = `  
                <img src=${this.imgSrc} alt=${this.alt}>
                <h3 class="menu__item-subtitle">Menu "${this.title}"</h3>
                <div class="menu__item-descr">${this.desk}</div>
                <div class="menu__item-divider"></div>
                <div class="menu__item-price">
                    <div class="menu__item-cost">Price:</div>
                    <div class="menu__item-total"><span>${this.prise}</span> UAH/day</div>
                </div> 
            `;

            this.parent.append(element);
        }
    }
     getResource('http://localhost:3000/menu')
        .then(data => {
            data.forEach(({img, altimg, title, descr, price}) => {
                new MenuCard(img, altimg, title, descr, price, '.menu .container').render();
            });
        }); 
        
    // Forms

    const forms = document.querySelectorAll('form');
    const message = {
        loading: 'src/img/form/spinner.svg',
        success: 'Спасибо! Скоро мы с вами свяжемся',
        failure: 'Что-то пошло не так...'
    };

    forms.forEach(item => {
        bindPostData(item);
    });

    const postData = async (url, data) => {
        let res = await fetch(url, {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: data
        });
    
        return await res.json();
    };

    async function getResource(url) {
        let res = await fetch(url);
    
        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`);
        }
    
        return await res.json();
    }

    function bindPostData(form) {
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            let statusMessage = document.createElement('img');
            statusMessage.src = message.loading;
            statusMessage.style.cssText = `
                display: block;
                margin: 0 auto;
            `;
            form.insertAdjacentElement('afterend', statusMessage);
        
            const formData = new FormData(form);

            const json = JSON.stringify(Object.fromEntries(formData.entries()));

            postData('http://localhost:3000/requests', json)
            .then(data => {
                console.log(data);
                showThanksModal(message.success);
                statusMessage.remove();
            }).catch(() => {
                showThanksModal(message.failure);
            }).finally(() => {
                form.reset();
            });
        });
    }

    function showThanksModal(message) {
        const prevModalDialog = document.querySelector('.modal__dialog');

        prevModalDialog.classList.add('hide');
        openModal();

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog');
        thanksModal.innerHTML = `
            <div class="modal__content">
                <div class="modal__close" data-close>×</div>
                <div class="modal__title">${message}</div>
            </div>
        `;
        document.querySelector('.modal').append(thanksModal);
        setTimeout(() => {
            thanksModal.remove();
            prevModalDialog.classList.add('show');
            prevModalDialog.classList.remove('hide');
            closeModal();
        }, 4000);
    }

    //Sliders
    const offerSlider = document.querySelector('.offer__slider'),
          offerSlides = offerSlider.querySelectorAll('.offer__slide'), 
          offerSliderWrapper = document.querySelector('.offer__slider-wrapper'),
          offerSlidesField = document.querySelector('.offer__slider-inner'),
          width = window.getComputedStyle(offerSliderWrapper).width,
          current = offerSlider.querySelector('#current'), 
          total = offerSlider.querySelector('#total'), 
          btnNext = offerSlider.querySelector('.offer__slider-next'),
          btnPrev = offerSlider.querySelector('.offer__slider-prev');

    let offerSlideIndex = 1; 
    let offset = 0;
    
    //type 1

    // function addZero(figures) {
    //     if (figures > 0 && figures < 10) {
    //         return '0' + figures;
    //     } else { return figures; }
    // }

    // function insertTotal(selector, where) {  
    //     const total = offerSlider.querySelector(where);
    //     const selectorTotal = selector.length; 
    //     total.textContent = addZero(selectorTotal);
    //     return selectorTotal;
    // } 

    // function insertCurrent(index) {
    //     current.textContent = addZero(index);
    // } 

    // const totalSlide = insertTotal(offerSlides, '#total');    
    // setActiveSlide();

    // function setActiveSlide() {
    //     insertCurrent(offerSlideIndex);
              
    //     if (offerSlideIndex > totalSlide) { 
    //         insertCurrent(offerSlideIndex = 1);  
    //     } else if (offerSlideIndex < 1) {
    //         insertCurrent(offerSlideIndex = totalSlide);
    //     } 
        
    //     offerSlide.forEach((item, index) => {  
    //         index = index + 1;
    //         item.classList.add('hide');

    //         if (index == offerSlideIndex) {
    //             item.classList.add('show');  
    //             item.classList.remove('hide');    
    //         } else {
    //             item.classList.add('hide');
    //             item.classList.remove('show');
    //         }
    //     });
    // }
    
    // btnNext.addEventListener('click', () => {  
    //     insertCurrent(++offerSlideIndex);  
    //     setActiveSlide();
    // });  

    // btnPrev.addEventListener('click', () => { 
    //     insertCurrent(--offerSlideIndex);   
    //     setActiveSlide();
    // });

    //type 2

    if (offerSlides.length < 10) {
        total.textContent = `0${offerSlides.length}`;
        current.textContent = `0${offerSlideIndex}`;
    } else {
        total.textContent = offerSlides.length;
        current.textContent = offerSlideIndex;
    }

    offerSlidesField.style.width = 100 * offerSlides.length + '%';
    offerSlidesField.style.display = 'flex';
    offerSlidesField.style.transition = '0.5s all';

    offerSliderWrapper.style.overflow = 'hidden';


    offerSlides.forEach(slide => {
        slide.style.width = width;
    });

    offerSlider.style.position = 'relative';

    const indicators = document.createElement('ol'),
          dots = [];

    function setActiveDot() { 
        dots.forEach(dot => dot.style.opacity = '.5');
        dots[offerSlideIndex - 1].style.opacity = 1;
    }

    function addZero() {
        if(offerSlides.length < 10) {
            current.textContent = `0${offerSlideIndex}`;
        } else {
            current.textContent =  offerSlideIndex;
        }
    }
    function deleteNotDigits(str) {
        return +str.replace(/\D/g, '');
    }

    indicators.classList.add('carousel-indicators');

    indicators.style.cssText = `
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 15;
        display: flex;
        justify-content: center;
        margin-right: 15%;
        margin-left: 15%;
        list-style: none;
    `;

    offerSlider.append(indicators);
    
    for(let i=0; i < offerSlides.length; i++) {
        const dot = document.createElement('li');
        dot.setAttribute('data-slide-to', i + 1);
        dot.style.cssText = `
            box-sizing: content-box;
            flex: 0 1 auto;
            width: 30px;
            height: 6px;
            margin-right: 3px;
            margin-left: 3px;
            cursor: pointer;
            background-color: #fff;
            background-clip: padding-box;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            opacity: .5;
            transition: opacity .6s ease;
        `;

        if(i == 0) {
            dot.style.opacity = "1";
        }

        indicators.append(dot);
        dots.push(dot);
    }

    btnNext.addEventListener('click',() => {
        if (offset == deleteNotDigits(width) * (offerSlides.length - 1)) {
            offset = 0;
        } else {
            offset += deleteNotDigits(width);
        }
        
        offerSlidesField.style.transform = `translateX(-${offset}px)`;

        if (offerSlideIndex == offerSlides.length) {
            offerSlideIndex = 1;
        } else {
            offerSlideIndex++;
        }

        addZero();
        setActiveDot();
    });

    btnPrev.addEventListener('click',() => {
        if (offset == 0) {
            offset = deleteNotDigits(width) * (offerSlides.length - 1);
        } else {
            offset -= deleteNotDigits(width);
        }
        
        offerSlidesField.style.transform = `translateX(-${offset}px)`;

        if (offerSlideIndex == 1) {
            offerSlideIndex = offerSlides.length;
        } else {
            offerSlideIndex--;
        }

        addZero();
        setActiveDot();
    });

    dots.forEach(dot => {
        dot.addEventListener('click', (e) => {
            const slideTo = e.target.getAttribute('data-slide-to');

            offerSlideIndex = slideTo;
            offset = offset = deleteNotDigits(width) * (slideTo - 1);
            offerSlidesField.style.transform = `translateX(-${offset}px)`;

            addZero();            
            setActiveDot();
        });
    });

});